import { withApollo } from '../lib/apollo';
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';

interface RocketInventoryData {
  authUser: {
    name: string;
  };
}

interface RocketInventoryVars {
  login: string;
  password: string;
}

export const ALL_POSTS_QUERY = gql`
  query($login: String, $password: String) {
    authUser(login: $login, password: $password){
      name
    }
  }
`

const IndexPage = (props) => {
  const { data } = useQuery<RocketInventoryData, RocketInventoryVars>(ALL_POSTS_QUERY, { variables: { login: 'log', password: "pas" } });
  return <div>Welcome to Next.js!{JSON.stringify(data.authUser.name)}</div>;
}

export default withApollo(IndexPage, {
  // Disable apollo ssr fetching in favour of automatic static optimization
  ssr: false,
})